<template>
    <div>
        <center>
            <h1 class="mt-5">Employee manager</h1>
        </center>
        <button type="button" class="btn btn-success m-lg-2" id="create">
            <i class="fas fa-plus"></i>
        </button>
        <table class="table align-middle" id="">
            <thead>
                <tr>
                    <th scope="col">ID</th>
                    <th scope="col">Full name</th>
                    <th scope="col">Gender</th>
                    <th scope="col">Job position</th>
                    <th scope="col">created</th>
                    <th scope="col">Action</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in list" :key="item.employeeId">
                    <th scope="row">{{item.fullName}}</th>
                    <td>{{item.gender}}</td>
                    <td>{{item.jobPosition}}</td>
                    <td>{{item.dayCreated}}</td>
                    <td><i class="btn btn-danger btn-sm px-3" style="font-style: initial;">High</i></td>
                    <td><i class="btn btn-dark btn-sm px-3" style="font-style: initial;">Open</i></td>
                    <td>
                        <button type="button" class="btn btn-warning btn-sm px-3" id="detail">
                            <i class="fas fa-file"></i>
                        </button>
                        <button type="button" class="btn btn-info btn-sm px-3" id="edit">
                            <i class="fas fa-pen"></i>
                        </button>
                        <button type="button" class="btn btn-danger btn-sm px-3" id="delete">
                            <i class="fas fa-times"></i>
                        </button>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>
<script>
    import Vue from 'vue';
    import axios from 'axios'
    import VueAxios from 'vue-axios'
    Vue.use(VueAxios, axios)
    export default {
        name: "Home",
        data() {
            return { list:undefined }
        },
        mounted() {
            Vue.axios.get('http://localhost:5000/api/employee/list') //
                .then((resp) => {
                    this.list = resp.data.data
                    console.warn(resp.data.data)
                })
        }
    }
</script>

